/*
  Josh's Custom CSS Reset
  https://www.joshwcomeau.com/css/custom-css-reset/
*/

*,
*::before,
*::after {
  box-sizing: border-box;
}

* {
  margin: 0;
}

html {
  interpolate-size: allow-keywords;
}

body {
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
}

img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
}

input,
button,
textarea,
select {
  font: inherit;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
}

p {
  text-wrap: pretty;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  text-wrap: balance;
}

#root,
#__next {
  isolation: isolate;
}

/* My styles  */

@font-face {
  font-family: 'Tulia Bold';
  src: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
}

html,
body {
  width: 250px;
  border: none;
  background-color: white;
  overflow: hidden;
}

body {
  display: flex;
  flex-direction: column;
  button:hover {
    cursor: pointer;
  }
}

header {
  display: flex;
  align-items: center;
  background-color: #023e8a;
  color: white;
  height: 40px;
  padding: 5px;
}

.logo {
  width: 30px;
}

#settings-btn {
  background: none;
  border: none;
  width: 30px;
  margin-left: auto;
}

.item-wrapper > input[type='radio'] {
  display: none;
}

.item-wrapper:not(:last-child) > .resource {
  display: block;
  border-bottom: 1px solid black;
}

.summary {
  display: flex;
  align-items: center;
  user-select: none;
  height: 40px;
  padding: 5px;
  font-size: 1.2rem;
  &:hover {
    cursor: pointer;
  }
  label[for='cambridge-dictionary'] > & {
    color: white;
    background-color: #1b224c;
    font-family: 'League Spartan', sans-serif;
  }
  label[for='collins'] > & {
    font-family: 'Tulia Bold';
  }
}

.details {
  display: none;
  padding: 10px;
  width: 100%;
}

input[type='radio']:checked + label > .details {
  display: block;
}

.search {
  display: flex;
  gap: 5px;
  & > input {
    min-width: 0;
  }
}
